import React, { useState, useEffect, useRef } from 'react';
import {
  Box,
  Typography,
  Card,
  CardContent,
  Button,
  Grid,
  Chip,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Alert,
  Paper
} from '@mui/material';
import {
  PlayArrow,
  Pause,
  Stop,
  DirectionsWalk,
  DirectionsBike,
  Terrain,
  Speed,
  Timer,
  Straighten,
  ArrowBack,
  GpsFixed,
  GpsOff,
  Height,
  FitnessCenter,
} from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import { useData } from '../context/DataContext';
import useGeolocation from '../hooks/useGeolocation';
import PigeonMap from '../components/PigeonMap';



// Calcula distancia en Km entre dos coordenadas
export const getDistanceFromLatLonInKm = (lat1, lon1, lat2, lon2) => {
  if (!lat1 || !lon1 || !lat2 || !lon2) return 0;
  
  const R = 6371; // Radio de la tierra en km
  const dLat = deg2rad(lat2 - lat1);
  const dLon = deg2rad(lon2 - lon1);
  const a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(deg2rad(lat1)) * Math.cos(deg2rad(lat2)) *
    Math.sin(dLon / 2) * Math.sin(dLon / 2);
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  const d = R * c; // Distancia en km
  return d;
};

const deg2rad = (deg) => {
  return deg * (Math.PI / 180);
};


const Training = () => {
  const navigate = useNavigate();
  const { addTraining, profile, setCurrentTraining } = useData();
  
  const [trainingState, setTrainingState] = useState('idle');
  const [selectedType, setSelectedType] = useState(null);
  const [trainingData, setTrainingData] = useState({
    type: '',
    startTime: null,
    currentTime: 0,
    distance: 0,
    currentSpeed: 0,
    avgSpeed: 0,
    maxSpeed: 0,
    route: [],
    altitude: 0,
    startAltitude: 0,
    elevationGain: 0,
    totalPausedTime: 0,
    lastValidPosition: null,
    movingTime: 0 //  NUEVO: tiempo en movimiento real
  });
  
  const [finishDialogOpen, setFinishDialogOpen] = useState(false);
  const [gpsError, setGpsError] = useState(false);
  const [gpsPermissionDenied, setGpsPermissionDenied] = useState(false);
  
  const intervalRef = useRef(null);
  const lastPositionRef = useRef(null);
  const pausedTimeRef = useRef(0);
  const lastMovingTimeRef = useRef(null);
  const lastSpeedsRef = useRef([]);
  const consecutiveRejectionsRef = useRef(0);      //  NUEVO (si lo usas)
  const stationaryStartTimeRef = useRef(null);     //  NUEVO
  const isStationaryRef = useRef(false);           //  NUEVO
  
  // Hook de geolocalizacin
  const { location, error, isWatching, startWatching, stopWatching } = useGeolocation();
  // ============================================
  // CONSTANTES DE FILTRADO GPS POR ACTIVIDAD
  // ============================================
  const SPEED_LIMITS = {
    caminar: { max: 12, min: 1.2 },     // 12 km/h - caminata rpida/trote
    bici: { max: 60, min: 3.0 },        // 60 km/h - ciclismo de ruta
    mtb: { max: 50, min: 2.5 }          // 45 km/h - MTB bajada
  };

  const MIN_ACCURACY = 50;        // Ignorar posiciones con precisin > 50m
  const MIN_DISTANCE = 0.005;     // Mnimo 1 metro para contar distancia
  const POSITION_TIMEOUT = 5000;  // Si no hay GPS por 5 segundos


  useEffect(() => {
  startWatching();  // Activar seguimiento GPS inmediatamente

  return () => {
    stopWatching(); // Limpiar al salir de la pantalla
  };
}, []);



  // Manejar errores de GPS
  useEffect(() => {
    if (error) {
      setGpsError(true);
      if (error.code === 1) {
        setGpsPermissionDenied(true);
      }
    } else {
      setGpsError(false);
      setGpsPermissionDenied(false);
    }
  }, [error]);

  // ============================================
  // INICIAR ENTRENAMIENTO
  // ============================================
   

  
  const startTraining = () => {
  if (!selectedType) {
    alert('Por favor selecciona un tipo de actividad');
    return;
  }
  
  if (!navigator.geolocation) {
    alert('Tu navegador no soporta geolocalizacin');
    return;
  }

  // Ya estamos observando GPS, no llamamos a startWatching()
  setCurrentTraining({ state: 'active', type: selectedType });

  setTrainingState('active');
  setTrainingData({
    type: selectedType,
    startTime: new Date(),
    currentTime: 0,
    distance: 0,
    currentSpeed: location?.speed ? location.speed * 3.6 : 0, // usar velocidad actual si existe
    avgSpeed: 0,
    maxSpeed: 0,
    route: [],
    altitude: location?.altitude || 0,
    startAltitude: location?.altitude || 0,
    elevationGain: 0,
    totalPausedTime: 0,
    lastValidPosition: null,
    movingTime: 0
  });
  
  // Resetear referencias para el nuevo entrenamiento
  pausedTimeRef.current = 0;
  lastPositionRef.current = location ? {
    lat: location.lat,
    lng: location.lng,
    altitude: location.altitude || 0,
    timestamp: location.timestamp
  } : null;
  lastMovingTimeRef.current = Date.now();
  lastSpeedsRef.current = [];
  isStationaryRef.current = false;
  stationaryStartTimeRef.current = null;
  consecutiveRejectionsRef.current = 0;
  
  intervalRef.current = setInterval(() => {
    setTrainingData(prev => ({
      ...prev,
      currentTime: prev.currentTime + 1
    }));
  }, 1000);
};

// ============================================
// FILTRO DE POSICIN GPS - POR ACTIVIDAD
// ============================================
useEffect(() => {
  if (trainingState !== 'active' || !location || !selectedType) return;

  const limits = SPEED_LIMITS[selectedType] || SPEED_LIMITS.caminar;
  const MIN_INSTANT_SPEED = limits.min;
  const MAX_INSTANT_SPEED = limits.max;

  // FILTRO 1: Precisin
  if (location.accuracy > MIN_ACCURACY) {
    console.log(` GPS impreciso: ${location.accuracy.toFixed(0)}m`);
    return;
  }

   // Inicializacin
  if (!lastPositionRef.current) {
    lastPositionRef.current = {
      lat: location.lat,
      lng: location.lng,
      altitude: location.altitude || 0,
      timestamp: location.timestamp
    };
    lastMovingTimeRef.current = Date.now();
    return;
  }

  const deltaTimeMs = location.timestamp - lastPositionRef.current.timestamp;
  if (deltaTimeMs < 500) return;

  const deltaTimeSec = deltaTimeMs / 1000;
  const distance = calculateDistance(
    lastPositionRef.current.lat,
    lastPositionRef.current.lng,
    location.lat,
    location.lng
  ); // km

  
  // Velocidad instantnea (priorizar GPS nativo si existe)
  const instantSpeed = location.speed !== null && location.speed !== undefined
    ? location.speed * 3.6
    : deltaTimeSec > 0 ? (distance / deltaTimeSec) * 3600 : 0;

  // FILTRO 2: Salto imposible
  const maxDistPossible = (limits.max / 3.6) * deltaTimeSec / 1000; // km
  if (distance > maxDistPossible * 1.2) {
    console.log(` Salto imposible: ${(distance*1000).toFixed(0)}m en ${deltaTimeSec.toFixed(1)}s`);
    return;
  }

  // FILTRO 2.5: Ratio de cambio de velocidad
  if (lastSpeedsRef.current.length > 0) {
    const lastSpeed = lastSpeedsRef.current[lastSpeedsRef.current.length - 1];
    const speedRatio = instantSpeed / (lastSpeed || 1);
    if (speedRatio > 3) {
      console.log(` Spike de velocidad: ${lastSpeed.toFixed(1)}  ${instantSpeed.toFixed(1)} km/h`);
      return;
    }
  }

   // ----- DETECTOR DE ESTACIONARIO -----
  if (instantSpeed < MIN_INSTANT_SPEED) {
    if (!isStationaryRef.current) {
      stationaryStartTimeRef.current = Date.now();
      isStationaryRef.current = true;
    } else {
      const stationarySec = (Date.now() - stationaryStartTimeRef.current) / 1000;
      if (stationarySec > 20) { // 20 segundos quieto
        // Reseteamos la referencia para no arrastrar un punto antiguo
        lastPositionRef.current = {
          lat: location.lat,
          lng: location.lng,
          altitude: location.altitude || 0,
          timestamp: location.timestamp
        };
        console.log(` Estacionario durante ${stationarySec.toFixed(0)}s, referencia actualizada`);
        return;
      }
    }
    console.log(` Drift: ${instantSpeed.toFixed(1)} km/h`);
    return;
  } else {
    // Salimos del estado estacionario
    isStationaryRef.current = false;
    stationaryStartTimeRef.current = null;
  }

  // FILTRO 4: Muy rpido  error GPS, ignorar
  if (instantSpeed > MAX_INSTANT_SPEED) {
    console.log(` Velocidad no realista: ${instantSpeed.toFixed(1)} km/h`);
    return;
  }
  // FILTRO 5: Distancia mnima para considerar movimiento real
  const expectedMinDistance = (MIN_INSTANT_SPEED / 3600) * deltaTimeSec;
  if (distance < expectedMinDistance * 0.5) {
    console.log(` Ruido despreciado: ${(distance*1000).toFixed(1)}m en ${deltaTimeSec.toFixed(1)}s`);
    // Actualizamos la referencia pero NO sumamos distancia
    lastPositionRef.current = {
      lat: location.lat,
      lng: location.lng,
      altitude: location.altitude || 0,
      timestamp: location.timestamp
    };
    return;
  }
  // === POSICIN VLIDA  procesar ===
  setTrainingData(prev => {
    let newDistance = prev.distance + distance;
    let newElevationGain = prev.elevationGain;
    let newMovingTime = prev.movingTime + deltaTimeSec; // solo sumamos tiempo si nos movemos
    let newMaxSpeed = Math.max(prev.maxSpeed, instantSpeed);

    // Ganancia de elevacin (limite por tiempo para evitar picos)
    if (location.altitude && lastPositionRef.current.altitude) {
      const elevDiff = location.altitude - lastPositionRef.current.altitude;
      const maxElevRate = 1000 / 3600; // ~1000 m/h mximo realista
      const maxElevPossible = maxElevRate * deltaTimeSec;
      if (elevDiff > 0 && elevDiff < Math.max(20, maxElevPossible)) {
        newElevationGain = prev.elevationGain + elevDiff;
      }
    }

    // Suavizado de velocidad actual (usa velocidades instantneas vlidas)
    lastSpeedsRef.current.push(instantSpeed);
    if (lastSpeedsRef.current.length > 5) lastSpeedsRef.current.shift();
    const smoothedSpeed = lastSpeedsRef.current.reduce((a,b) => a+b, 0) / lastSpeedsRef.current.length;

    // Velocidad promedio real (solo tiempo en movimiento)
    const avgSpeed = newMovingTime > 0 ? (newDistance / (newMovingTime / 3600)) : 0;

    // Nuevo punto en ruta
    const newRoutePoint = {
      lat: location.lat,
      lng: location.lng,
      altitude: location.altitude || 0,
      time: prev.currentTime,
      segmentTime: deltaTimeSec,
      speed: smoothedSpeed,
      distance: newDistance
    };

    return {
      ...prev,
      distance: newDistance,
      currentSpeed: smoothedSpeed,
      avgSpeed: avgSpeed,
      maxSpeed: Math.min(newMaxSpeed, limits.max), // lmite por actividad
      altitude: location.altitude || prev.altitude,
      startAltitude: prev.startAltitude === 0 ? (location.altitude || 0) : prev.startAltitude,
      elevationGain: newElevationGain,
      movingTime: newMovingTime,
      route: [...prev.route, newRoutePoint]
    };
  });

  // Actualizar ltima posicin vlida
    lastPositionRef.current = {
    lat: location.lat,
    lng: location.lng,
    altitude: location.altitude || 0,
    timestamp: location.timestamp
  };
  lastMovingTimeRef.current = Date.now();

}, [location, trainingState, selectedType]);
  // ============================================
  // CALCULAR DISTANCIA (Frmula de Haversine)
  // ============================================
  const calculateDistance = (lat1, lon1, lat2, lon2) => {
    const R = 6371; // Radio Tierra en km
    const dLat = deg2rad(lat2 - lat1);
    const dLon = deg2rad(lon2 - lon1);
    const a = 
      Math.sin(dLat/2) * Math.sin(dLat/2) +
      Math.cos(deg2rad(lat1)) * Math.cos(deg2rad(lat2)) * 
      Math.sin(dLon/2) * Math.sin(dLon/2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
    return R * c;
  };

  const deg2rad = (deg) => deg * (Math.PI/180);

  // ============================================
  // CONTROLES DE ENTRENAMIENTO
  // ============================================
  const pauseTraining = () => {
    setTrainingState('paused');
    stopWatching();
    clearInterval(intervalRef.current);
    setCurrentTraining({ state: 'paused', type: trainingData.type });
    pausedTimeRef.current = Date.now();
    lastMovingTimeRef.current = null;
  };

  const resumeTraining = () => {
    setTrainingState('active');
    startWatching();
    setCurrentTraining({ state: 'active', type: trainingData.type });
    
    if (pausedTimeRef.current > 0) {
      const pauseDuration = (Date.now() - pausedTimeRef.current) / 1000;
      setTrainingData(prev => ({
        ...prev,
        totalPausedTime: prev.totalPausedTime + pauseDuration
      }));
      pausedTimeRef.current = 0;
    }
    
    lastMovingTimeRef.current = Date.now();
    
    intervalRef.current = setInterval(() => {
      setTrainingData(prev => ({
        ...prev,
        currentTime: prev.currentTime + 1
      }));
    }, 1000);
  };

  const finishTraining = () => {
    stopWatching();
    clearInterval(intervalRef.current);
    setFinishDialogOpen(true);
  };

  // ============================================
// FINALIZAR Y CALCULAR ESTADSTICAS REALES - CORREGIDO
// ============================================
const confirmFinish = () => {
  const endTime = new Date();
  
  const totalDistance = trainingData.distance;
  const totalMovingSeconds = trainingData.movingTime;
  const totalCurrentSeconds = trainingData.currentTime;
  
  //  VALIDAR VELOCIDAD MXIMA SEGN ACTIVIDAD
  const validatedMaxSpeed = Math.min(
    trainingData.maxSpeed,
    SPEED_LIMITS[trainingData.type]?.max || 60
  );
  
  // Calcular ritmo promedio
  let avgPace = '0:00';
  if (totalDistance > 0 && totalMovingSeconds > 0) {
    const paceInSeconds = totalMovingSeconds / totalDistance;
    const paceMinutes = Math.floor(paceInSeconds / 60);
    const paceSecs = Math.floor(paceInSeconds % 60);
    avgPace = `${paceMinutes}:${paceSecs.toString().padStart(2, '0')}`;
  }

  // Calcular caloras
  let caloriesBurned = 0;
  if (profile?.weight && totalDistance > 0) {
    const met = selectedType === 'caminar' ? 3.8 : selectedType === 'bici' ? 6.8 : 8.5;
    const hours = totalMovingSeconds / 3600;
    caloriesBurned = Math.round(met * profile.weight * hours);
  }


  //  CREAR SPLITS POR KM - Usando distancia acumulada
  const splits = [];
  let lastSplitKm = 0;
  let lastSplitTime = 0;
  
  if (trainingData.route.length > 0) {
    trainingData.route.forEach((point, index) => {
      if (index === 0) {
        lastSplitTime = point.time;
        return;
      }
      
      const pointDistance = point.distance || 0;
      
      if (Math.floor(pointDistance) > lastSplitKm) {
        const splitTime = point.time - lastSplitTime;
        const splitMinutes = Math.floor(splitTime / 60);
        const splitSeconds = splitTime % 60;
        
        splits.push({
          km: lastSplitKm + 1,
          pace: `${splitMinutes}:${splitSeconds.toString().padStart(2, '0')}`,
          time: splitTime
        });
        
        lastSplitKm = Math.floor(pointDistance);
        lastSplitTime = point.time;
      }
    });
  }

  // Crear objeto de entrenamiento
  const newTraining = {
    id: Date.now(),
    type: trainingData.type,
    date: trainingData.startTime.toISOString(),
    startTime: trainingData.startTime.toLocaleTimeString('es-ES', { hour: '2-digit', minute: '2-digit' }),
    endTime: endTime.toLocaleTimeString('es-ES', { hour: '2-digit', minute: '2-digit' }),
    movingTime: Math.round(totalMovingSeconds),
    totalTime: Math.round(totalCurrentSeconds),
    distance: parseFloat(totalDistance.toFixed(2)),
    avgSpeed: totalMovingSeconds > 0 
      ? parseFloat((totalDistance / (totalMovingSeconds / 3600)).toFixed(1)) 
      : 0,
    maxSpeed: parseFloat(validatedMaxSpeed.toFixed(1)),
    avgPace: avgPace,
    elevationGain: Math.round(trainingData.elevationGain),
    elevationLoss: 0,
    startAltitude: Math.round(trainingData.startAltitude),
    endAltitude: Math.round(trainingData.altitude),
    calories: caloriesBurned,
    route: trainingData.route,
    splits: splits
  };

  console.log(' Entrenamiento guardado:', newTraining);
  
  addTraining(newTraining);
  setFinishDialogOpen(false);
  setTrainingState('idle');
  setSelectedType(null);
  setCurrentTraining(null);
  navigate('/home');
};

  const formatTime = (seconds) => {
    if (!seconds || seconds < 0) return '00:00:00';
    const hrs = Math.floor(seconds / 3600);
    const mins = Math.floor((seconds % 3600) / 60);
    const secs = Math.floor(seconds % 60);
    return `${hrs.toString().padStart(2, '0')}:${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
  };

  const handleSelectType = (type) => {
    setSelectedType(type);
  };

  return (
    <Box sx={{ p: 2, pb: 10 }}>
      {/* Header */}
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
        <IconButton onClick={() => navigate('/home')} sx={{ mr: 1 }}>
          <ArrowBack />
        </IconButton>
        <Typography variant="h5" sx={{ fontWeight: 700 }}>
          {trainingState === 'idle' ? 'Nuevo entrenamiento' : 
           trainingState === 'active' ? 'Entrenando' : 'Entrenamiento pausado'}
        </Typography>
      </Box>

      {/* Alerta de GPS */}
      {gpsPermissionDenied && (
        <Alert severity="error" sx={{ mb: 2 }}>
          Permiso de ubicacin denegado. Actvalo en la configuracin de tu navegador.
        </Alert>
      )}
      
      {gpsError && !gpsPermissionDenied && trainingState !== 'idle' && (
        <Alert severity="warning" sx={{ mb: 2 }}>
          Esperando seal GPS... Asegrate de tener visibilidad del cielo.
        </Alert>
      )}

      {/* Mapa con Pigeon Maps */}
      <PigeonMap 
        route={trainingData.route}
        currentLocation={location}
        isActive={trainingState !== 'idle'}
      />

      {/* Estado GPS */}
      {trainingState !== 'idle' && (
        <Card sx={{ 
          mt: 2, 
          mb: 2, 
          bgcolor: isWatching ? '#E8F5E9' : '#FFEBEE',
          borderRadius: 2
        }}>
          <CardContent sx={{ py: 1.5, '&:last-child': { pb: 1.5 } }}>
            <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                {isWatching ? (
                  <GpsFixed sx={{ color: '#4CAF50' }} />
                ) : (
                  <GpsOff sx={{ color: '#F44336' }} />
                )}
                <Typography variant="body2" sx={{ fontWeight: 500 }}>
                  {isWatching ? 'GPS Conectado' : 'GPS Desconectado'}
                </Typography>
              </Box>
              {location && (
                <Typography variant="caption" sx={{ color: 'text.secondary' }}>
                  Precisin: {location.accuracy?.toFixed(0) || '?'}m
                </Typography>
              )}
            </Box>
          </CardContent>
        </Card>
      )}

      {/* Selector de actividad */}
      {trainingState === 'idle' && (
        <Card sx={{ mt: 3 }}>
          <CardContent>
            <Typography variant="subtitle1" sx={{ fontWeight: 600, mb: 2 }}>
              Selecciona actividad
            </Typography>
            <Grid container spacing={2}>
              <Grid item xs={4}>
                <Paper
                  elevation={selectedType === 'caminar' ? 3 : 1}
                  sx={{ 
                    textAlign: 'center', 
                    p: 2,
                    cursor: 'pointer',
                    bgcolor: selectedType === 'caminar' ? '#E8F5E9' : 'background.paper',
                    border: selectedType === 'caminar' ? '2px solid #2E7D32' : '2px solid transparent',
                    transition: 'all 0.2s',
                    '&:hover': { 
                      bgcolor: '#F5F5F5',
                      transform: 'translateY(-2px)'
                    }
                  }}
                  onClick={() => handleSelectType('caminar')}
                >
                  <DirectionsWalk sx={{ fontSize: 40, color: '#2E7D32' }} />
                  <Typography variant="body2" sx={{ mt: 1, fontWeight: 500 }}>
                    Caminar
                  </Typography>
                </Paper>
              </Grid>
              <Grid item xs={4}>
                <Paper
                  elevation={selectedType === 'bici' ? 3 : 1}
                  sx={{ 
                    textAlign: 'center', 
                    p: 2,
                    cursor: 'pointer',
                    bgcolor: selectedType === 'bici' ? '#E3F2FD' : 'background.paper',
                    border: selectedType === 'bici' ? '2px solid #1976D2' : '2px solid transparent',
                    transition: 'all 0.2s',
                    '&:hover': { 
                      bgcolor: '#F5F5F5',
                      transform: 'translateY(-2px)'
                    }
                  }}
                  onClick={() => handleSelectType('bici')}
                >
                  <DirectionsBike sx={{ fontSize: 40, color: '#1976D2' }} />
                  <Typography variant="body2" sx={{ mt: 1, fontWeight: 500 }}>
                    Bici
                  </Typography>
                </Paper>
              </Grid>
              <Grid item xs={4}>
                <Paper
                  elevation={selectedType === 'mtb' ? 3 : 1}
                  sx={{ 
                    textAlign: 'center', 
                    p: 2,
                    cursor: 'pointer',
                    bgcolor: selectedType === 'mtb' ? '#EFEBE9' : 'background.paper',
                    border: selectedType === 'mtb' ? '2px solid #8D6E63' : '2px solid transparent',
                    transition: 'all 0.2s',
                    '&:hover': { 
                      bgcolor: '#F5F5F5',
                      transform: 'translateY(-2px)'
                    }
                  }}
                  onClick={() => handleSelectType('mtb')}
                >
                  <Terrain sx={{ fontSize: 40, color: '#8D6E63' }} />
                  <Typography variant="body2" sx={{ mt: 1, fontWeight: 500 }}>
                    MTB
                  </Typography>
                </Paper>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      )}

      {/* Mtricas en tiempo real */}
      {trainingState !== 'idle' && (
        <Card sx={{ mt: 3 }}>
          <CardContent>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
              <Chip 
                label={trainingData.type === 'mtb' ? 'MTB' : trainingData.type}
                icon={
                  trainingData.type === 'caminar' ? <DirectionsWalk /> :
                  trainingData.type === 'bici' ? <DirectionsBike /> : <Terrain />
                }
                sx={{ 
                  bgcolor: 
                    trainingData.type === 'caminar' ? '#E8F5E9' :
                    trainingData.type === 'bici' ? '#E3F2FD' : '#EFEBE9',
                  color: 
                    trainingData.type === 'caminar' ? '#2E7D32' :
                    trainingData.type === 'bici' ? '#1976D2' : '#8D6E63',
                  fontWeight: 600,
                  textTransform: 'capitalize'
                }}
              />
              <Typography variant="h6" sx={{ fontWeight: 700, fontFamily: 'monospace' }}>
                {formatTime(trainingData.currentTime)}
              </Typography>
            </Box>

            <Grid container spacing={2}>
              <Grid item xs={6}>
                <Card variant="outlined" sx={{ p: 1.5, borderRadius: 2 }}>
                  <Typography variant="caption" color="text.secondary">
                    <Straighten sx={{ fontSize: 14, mr: 0.5 }} />
                    Distancia
                  </Typography>
                  <Typography variant="h5" sx={{ fontWeight: 700 }}>
                    {trainingData.distance < 1 
                      ? `${Math.round(trainingData.distance * 1000)} m`  
                      : `${trainingData.distance.toFixed(2)} km`        
                    }
                  </Typography>
                </Card>
              </Grid>
              <Grid item xs={6}>
                <Card variant="outlined" sx={{ p: 1.5, borderRadius: 2 }}>
                  <Typography variant="caption" color="text.secondary">
                    <Speed sx={{ fontSize: 14, mr: 0.5 }} />
                    Velocidad
                  </Typography>
                  <Typography variant="h5" sx={{ fontWeight: 700 }}>
                    {trainingData.currentSpeed.toFixed(1)} km/h
                  </Typography>
                </Card>
              </Grid>
              <Grid item xs={6}>
                <Card variant="outlined" sx={{ p: 1.5, borderRadius: 2 }}>
                  <Typography variant="caption" color="text.secondary">
                    <Timer sx={{ fontSize: 14, mr: 0.5 }} />
                    Promedio
                  </Typography>
                  <Typography variant="h5" sx={{ fontWeight: 700 }}>
                    {trainingData.avgSpeed.toFixed(1)} km/h
                  </Typography>
                </Card>
              </Grid>
              <Grid item xs={6}>
                <Card variant="outlined" sx={{ p: 1.5, borderRadius: 2 }}>
                  <Typography variant="caption" color="text.secondary">
                    <FitnessCenter sx={{ fontSize: 14, mr: 0.5 }} />
                    Mxima
                  </Typography>
                  <Typography variant="h5" sx={{ fontWeight: 700 }}>
                    {trainingData.maxSpeed.toFixed(1)} km/h
                  </Typography>
                </Card>
              </Grid>
              <Grid item xs={6}>
                <Card variant="outlined" sx={{ p: 1.5, borderRadius: 2 }}>
                  <Typography variant="caption" color="text.secondary">
                    <Height sx={{ fontSize: 14, mr: 0.5 }} />
                    Altitud
                  </Typography>
                  <Typography variant="h5" sx={{ fontWeight: 700 }}>
                    {Math.round(trainingData.altitude)} m
                  </Typography>
                </Card>
              </Grid>
              <Grid item xs={6}>
                <Card variant="outlined" sx={{ p: 1.5, borderRadius: 2 }}>
                  <Typography variant="caption" color="text.secondary">
                    <Terrain sx={{ fontSize: 14, mr: 0.5 }} />
                    Desnivel +
                  </Typography>
                  <Typography variant="h5" sx={{ fontWeight: 700, color: '#4CAF50' }}>
                    +{Math.round(trainingData.elevationGain)} m
                  </Typography>
                </Card>
              </Grid>
            </Grid>

            {/* Tiempo en movimiento real */}
            <Box sx={{ mt: 2, textAlign: 'center' }}>
              <Typography variant="caption" color="text.secondary">
                Tiempo en movimiento: {formatTime(trainingData.movingTime)}
              </Typography>
            </Box>
          </CardContent>
        </Card>
      )}

      {/* Botones de control */}
      {trainingState !== 'idle' && (
        <Box sx={{ display: 'flex', justifyContent: 'center', gap: 2, mt: 3 }}>
          {trainingState === 'active' ? (
            <>
              <Button
                variant="contained"
                color="warning"
                size="large"
                startIcon={<Pause />}
                onClick={pauseTraining}
                sx={{ 
                  px: 4, 
                  py: 1.5, 
                  borderRadius: 3,
                  flex: 1,
                  maxWidth: 150
                }}
              >
                Pausa
              </Button>
              <Button
                variant="contained"
                color="error"
                size="large"
                startIcon={<Stop />}
                onClick={finishTraining}
                sx={{ 
                  px: 4, 
                  py: 1.5, 
                  borderRadius: 3,
                  flex: 1,
                  maxWidth: 150
                }}
              >
                Finalizar
              </Button>
            </>
          ) : (
            <>
              <Button
                variant="contained"
                color="success"
                size="large"
                startIcon={<PlayArrow />}
                onClick={resumeTraining}
                sx={{ 
                  px: 4, 
                  py: 1.5, 
                  borderRadius: 3,
                  flex: 1,
                  maxWidth: 150
                }}
              >
                Reanudar
              </Button>
              <Button
                variant="contained"
                color="error"
                size="large"
                startIcon={<Stop />}
                onClick={finishTraining}
                sx={{ 
                  px: 4, 
                  py: 1.5, 
                  borderRadius: 3,
                  flex: 1,
                  maxWidth: 150
                }}
              >
                Finalizar
              </Button>
            </>
          )}
        </Box>
      )}

      {/* Botn de comenzar */}
      {trainingState === 'idle' && (
        <Button
          fullWidth
          variant="contained"
          size="large"
          disabled={!selectedType}
          onClick={startTraining}
          sx={{ 
            mt: 3, 
            py: 1.5, 
            fontSize: '1.1rem',
            borderRadius: 3,
            bgcolor: selectedType ? 'primary.main' : 'action.disabledBackground',
            '&:hover': {
              bgcolor: selectedType ? 'primary.dark' : 'action.disabledBackground'
            }
          }}
        >
          Comenzar entrenamiento
        </Button>
      )}

      {/* Dilogo de finalizar */}
      <Dialog 
        open={finishDialogOpen} 
        onClose={() => setFinishDialogOpen(false)}
        PaperProps={{
          sx: { borderRadius: 3, p: 1 }
        }}
      >
        <DialogTitle sx={{ fontWeight: 700 }}>
          Finalizar entrenamiento
        </DialogTitle>
        <DialogContent>
          <Typography variant="body1" gutterBottom>
            Ests seguro de que quieres finalizar?
          </Typography>
          <Paper 
            elevation={0} 
            sx={{ 
              mt: 2, 
              p: 2, 
              bgcolor: 'background.default', 
              borderRadius: 2 
            }}
          >
            <Grid container spacing={2}>
              <Grid item xs={6}>
                <Typography variant="body2" color="text.secondary">
                  Distancia
                </Typography>
                <Typography variant="h6" sx={{ fontWeight: 700 }}>
                  {trainingData.distance.toFixed(2)} km
                </Typography>
              </Grid>
              <Grid item xs={6}>
                <Typography variant="body2" color="text.secondary">
                  Tiempo en movimiento
                </Typography>
                <Typography variant="h6" sx={{ fontWeight: 700, fontFamily: 'monospace' }}>
                  {formatTime(trainingData.movingTime)}
                </Typography>
              </Grid>
            </Grid>
          </Paper>
        </DialogContent>
        <DialogActions sx={{ p: 2, pt: 0 }}>
          <Button 
            onClick={() => setFinishDialogOpen(false)}
            variant="outlined"
            sx={{ borderRadius: 2 }}
          >
            Cancelar
          </Button>
          <Button 
            onClick={confirmFinish} 
            variant="contained" 
            color="error"
            sx={{ borderRadius: 2 }}
          >
            Finalizar
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default Training;