import React from 'react';
import { Box, Typography, Paper } from '@mui/material';
import { Map, Marker } from "pigeon-maps";


const PigeonMap = ({ route, currentLocation, isActive }) => {
  // Proveedores de mapas gratuitos (OSM, Stamen, etc.)
  const providers = {
    osm: (x, y, z) => `https://a.tile.openstreetmap.org/${z}/${x}/${y}.png`,
    stamen: (x, y, z) => `https://stamen-tiles.a.ssl.fastly.net/terrain/${z}/${x}/${y}.jpg`,
    carto: (x, y, z) => `https://a.basemaps.cartocdn.com/light_all/${z}/${x}/${y}.png`,
  };

  // Determinar centro del mapa
  const getCenter = () => {
    if (currentLocation) {
      return [currentLocation.lat, currentLocation.lng];
    }
    if (route.length > 0) {
      return [route[0].lat, route[0].lng];
    }
    return [40.416775, -3.703790]; // Madrid por defecto
  };

  // Calcular zoom inicial
  const getZoom = () => {
    if (route.length > 0 || currentLocation) return 15;
    return 12;
  };

  // Estado inicial
  if (!isActive && route.length === 0) {
    return (
      <Box sx={{
        height: 350,
        background: 'linear-gradient(145deg, #2C3E50 0%, #3498DB 100%)',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        color: 'white',
        borderRadius: 3,
        flexDirection: 'column',
        gap: 2
      }}>
        <Typography variant="h2" sx={{ fontSize: 48 }}></Typography>
        <Typography variant="h6">Esperando seal GPS...</Typography>
        <Typography variant="body2" sx={{ opacity: 0.8 }}>
          Activa la ubicacin en tu dispositivo
        </Typography>
      </Box>
    );
  }

  return (
    <Box sx={{ 
      height: 350, 
      width: '100%', 
      borderRadius: 3, 
      overflow: 'hidden',
      position: 'relative',
      boxShadow: '0 4px 12px rgba(0,0,0,0.1)'
    }}>
      <Map
        center={getCenter()}
        zoom={getZoom()}
        provider={providers.carto} // Cambia a osm, stamen, etc. si prefieres
        width="100%"
        height="100%"
        animate={true}
        zoomSnap={true}
        attribution={false} // Oculta atribucin para ms espacio
      >
        {/* Dibujar la ruta (lnea entre puntos) */}
        {route.length > 1 && route.map((point, index) => {
          if (index === 0) return null;
          const prev = route[index - 1];
          return (
            <React.Fragment key={index}>
              {/* Lnea entre puntos - usando Marker personalizado */}
              <Marker 
                anchor={[prev.lat, prev.lng]} 
                payload={[point.lat, point.lng]}
                color="transparent"
              />
            </React.Fragment>
          );
        })}

        {/* Marcador de INICIO */}
        {route.length > 0 && (
          <Marker
            anchor={[route[0].lat, route[0].lng]}
            payload={1}
            render={({ left, top }) => (
              <div
                style={{
                  position: 'absolute',
                  left: left - 12,
                  top: top - 12,
                  width: 24,
                  height: 24,
                  backgroundColor: '#4CAF50',
                  border: '3px solid white',
                  borderRadius: '50%',
                  boxShadow: '0 2px 8px rgba(0,0,0,0.3)',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  color: 'white',
                  fontSize: 12,
                  fontWeight: 'bold'
                }}
              >
                S
              </div>
            )}
          />
        )}

        {/* Marcador de POSICIN ACTUAL */}
        {currentLocation && (
          <Marker
            anchor={[currentLocation.lat, currentLocation.lng]}
            payload={2}
            render={({ left, top }) => (
              <div
                style={{
                  position: 'absolute',
                  left: left - 16,
                  top: top - 16,
                  width: 32,
                  height: 32,
                  backgroundColor: '#FC4C02',
                  border: '3px solid white',
                  borderRadius: '50%',
                  boxShadow: '0 0 15px rgba(252,76,2,0.5)',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  color: 'white',
                  fontSize: 14,
                  fontWeight: 'bold',
                  animation: 'pulse 2s infinite',
                  '@keyframes pulse': {
                    '0%': { transform: 'scale(1)', boxShadow: '0 0 0 0 rgba(252,76,2,0.7)' },
                    '70%': { transform: 'scale(1.1)', boxShadow: '0 0 0 10px rgba(252,76,2,0)' },
                    '100%': { transform: 'scale(1)', boxShadow: '0 0 0 0 rgba(252,76,2,0)' },
                  }
                }}
              >
                <div style={{
                  width: 12,
                  height: 12,
                  backgroundColor: 'white',
                  borderRadius: '50%'
                }} />
              </div>
            )}
          />
        )}

        {/* Dibujar lnea de ruta completa usando divs superpuestos */}
        {route.length > 1 && (
          <svg
            style={{
              position: 'absolute',
              top: 0,
              left: 0,
              width: '100%',
              height: '100%',
              pointerEvents: 'none',
              zIndex: 5
            }}
          >
            <polyline
              points={route.map(p => {
                // Convertir coordenadas geogrficas a pxeles
                const zoom = getZoom();
                const center = getCenter();
                // Aproximacin simple - en produccin usa proyeccin real
                return `${(p.lng - center[1]) * 1000 + 50},${(center[0] - p.lat) * 1000 + 50}`;
              }).join(' ')}
              style={{
                fill: 'none',
                stroke: '#FC4C02',
                strokeWidth: 4,
                strokeLinecap: 'round',
                strokeLinejoin: 'round',
                opacity: 0.8
              }}
            />
          </svg>
        )}
      </Map>

      {/* Panel de informacin GPS */}
      {currentLocation && (
        <Paper
          elevation={3}
          sx={{
            position: 'absolute',
            bottom: 16,
            left: 16,
            right: 16,
            p: 1.5,
            bgcolor: 'rgba(0,0,0,0.8)',
            color: 'white',
            borderRadius: 2,
            backdropFilter: 'blur(5px)',
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            zIndex: 10
          }}
        >
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <Box sx={{ 
              width: 10, 
              height: 10, 
              borderRadius: '50%', 
              bgcolor: currentLocation.accuracy < 20 ? '#4CAF50' : '#FFC107',
              animation: 'pulse 1.5s infinite'
            }} />
            <Typography variant="body2" sx={{ fontWeight: 500 }}>
              GPS: {currentLocation.accuracy?.toFixed(0) || 0}m
            </Typography>
          </Box>
          <Typography variant="body2">
             {currentLocation.altitude?.toFixed(0) || 0}m
          </Typography>
          <Typography variant="body2" sx={{ fontWeight: 600, color: '#FC4C02' }}>
             {currentLocation.speed?.toFixed(1) || 0} km/h
          </Typography>
        </Paper>
      )}
    </Box>
  );
};

export default PigeonMap;