// hooks/useGeolocation.js
import { useState, useEffect, useRef } from 'react';

const useGeolocation = (options = {}) => {
  const [location, setLocation] = useState(null);
  const [error, setError] = useState(null);
  const watchIdRef = useRef(null);

  // Opciones optimizadas para tracking deportivo
  const defaultOptions = {
    enableHighAccuracy: true,
    timeout: 10000,
    maximumAge: 0,
    ...options
  };

  const startWatching = () => {
    if (!navigator.geolocation) {
      setError('La geolocalizacin no es soportada');
      return;
    }

    // Si ya estamos observando, no duplicar
    if (watchIdRef.current !== null) return;

    watchIdRef.current = navigator.geolocation.watchPosition(
      (position) => {
        const { latitude, longitude, accuracy, altitude, speed, heading } = position.coords;
        
        setLocation({
          lat: latitude,
          lng: longitude,
          accuracy, // Importante para filtrar puntos malos
          altitude: altitude || 0,
          speed: speed ? speed * 3.6 : 0, // m/s a km/h
          heading,
          timestamp: position.timestamp
        });
        setError(null);
      },
      (err) => setError({ code: err.code, message: err.message }),
      defaultOptions
    );
  };

  const stopWatching = () => {
    if (watchIdRef.current !== null) {
      navigator.geolocation.clearWatch(watchIdRef.current);
      watchIdRef.current = null;
    }
  };

  // Limpieza automtica al desmontar
  useEffect(() => {
    return () => stopWatching();
  }, []);

  return { location, error, startWatching, stopWatching };
};

export default useGeolocation;